import React from 'react'

export default class Admin extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            newName: "",
            newBrand: '',
            amount: '',

            items: [],
            
            addOpen: false,

            rerender: false
        };
    }

    componentDidMount() {
        this.getItems()
    }

    componentDidUpdate() {
        if(this.state.rerender) {
            this.setState({rerender: false})
            this.getItems()
        }

    }

	render()	{

        return (
            <div style={{position: 'absolute', height: '100%', width: '100%', background: '#eeeeee', display: 'flex', flexDirection: 'column'}}>
                {/*Top nav bar */}
                <div style={{width: '100%', height: '70px', background: '#FFF96D', boxShadow: '2px 2px 10px #BCBCBC', display: 'flex', alignItems: 'center'}}>
                    <div style={{marginLeft: '10px', marginRight: '10px', height: '100%', transition: 'all 0.3s ease', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'default', fontSize: '30px', color: "#7B7B7B"}}>
                        {this.props.name}
                    </div>

                    
                    
                </div>

                <div style={{width: '100%', height: 'calc(100% - 70px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    <div style={{height: 'calc(100% - 20px)', width: 'calc(100% - 20px)', background: 'white', display: 'flex', flexWrap: 'wrap'}}>

                        <div onClick={this.openAdd} style={{cursor: 'point', display: 'flex', margin: '10px', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center', textAlign: 'center', fontSize: '40px'}}>
                            +
                        </div>
                        {
                            this.state.items.map((prop, key) =>
                                <div style={{cursor: 'point', display: 'flex', margin: '10px', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center', textAlign: 'center'}}>
                                    {prop.brand+ " " + prop.name}
                                </div>
                            )
                        }
                    </div>
                </div>
                    
                {this.state.addOpen &&
                    <div style={{position: 'absolute', height: '100%', width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                        <div onClick={this.closeAdd} style={{zIndex: 9, position: 'absolute', height: '100%', width: '100%', background: 'black', opacity: 0.3}}></div>

                        <div style={{position: 'relative', zIndex: 10, height: '350px', width: '250px', background: 'white', textAlign: 'center', display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                            <div style={{marginTop: '10px', marginBottom: '10px', fontSize: '30px'}}>Create Item</div>

                            <div style={{marginTop: '20px'}}>name</div>
                            <input onChange={this.setName} value={this.state.newName} style={{border: 'none', borderBottom: '2px solid #7B7B7B', outline: 'none', background: 'transparent'}} />

                            <div style={{marginTop: '20px'}}>brand</div>
                            <input onChange={this.setBrand} value={this.state.newBrand} style={{border: 'none', borderBottom: '2px solid #7B7B7B', outline: 'none', background: 'transparent'}} />

                            <div style={{marginTop: '20px'}}>amount</div>
                            <input onChange={this.setAmount} value={this.state.amount} style={{border: 'none', borderBottom: '2px solid #7B7B7B', outline: 'none', background: 'transparent'}} />

                            <div onClick={this.createItem} style={{cursor: 'pointer', marginTop: '10px', bottom: '10px', padding: '10px 20px', background: 'lightgrey'}}>create</div>
                        </div>
                    </div>
                }
            </div>
        )
    }

    setAmount = (e) => {
        this.setState({amount: e.target.value})
    }

    setBrand = (e) => {
        this.setState({newBrand: e.target.value})
    }

    setName = (e) => {
        this.setState({newName: e.target.value})
    }

    closeAdd = () => {
        this.setState({addOpen: false})
    }

    openAdd = () => {
        this.setState({addOpen: true})
    }

    createItem = async () => {
        try {
            var data = {name: this.state.newName, brand: this.state.newBrand, shopname: this.props.name, amount: this.state.amount}
            var response = await fetch(this.props.url+"/api/items", {
                method: 'POST', // *GET, POST, PUT, DELETE, etc.
                credentials: 'same-origin', // include, *same-origin, omit
                headers: {
                'Content-Type': 'application/json'
                },
                body: JSON.stringify(data) // body data type must match "Content-Type" header
            });
            console.log(response.json())

            this.setState({rerender: true, addOpen: false})

            this.forceUpdate()
        }
        catch(error) {
            this.setState({rerender: true, addOpen: false})

            this.forceUpdate()
        }
    }

    getItems = async () => {
        const response = await fetch(this.props.url+'/api/items');
        console.log(response)
        const data = await response.json();
        console.log(data)
        var items = []
        
        for(var i = 0; i < data.length; i++) {
            if(data[i].shopname === this.props.name)
                items.push(data[i])
        }

        console.log(items)

        this.setState({items})
    }
}

