import React from 'react'
import { MdRadioButtonUnchecked, MdRadioButtonChecked, MdKeyboardArrowLeft } from 'react-icons/md';
export default class User extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            topNavSelected: 'shops',
            shoppingList: [
                /*{
                    description: 'mayo',
                    item: {
                        name: 'mayonaise', 
                        brand: 'no name', 
                        shopname: 'Pick n Pay', 
                        amount: 50
                    }
                },
                {
                    description: 'mayo',
                    item: {
                        name: 'mayonaise', 
                        brand: 'no name', 
                        shopname: 'Pick n Pay', 
                        amount: 50
                    }
                },

                {
                    description: 'mayo',
                    item: null
                },
*/
            ],
            openNavItem: -1,

            items: [],

            shops: [],

            resultsIndex: -1,
            results:[],

            shopPage: "",
        };
    }

    componentDidMount() {
        this.test()
    }

	render()	{

        return (
            <div style={{position: 'absolute', height: '100%', width: '100%', background: '#eeeeee', display: 'flex', flexDirection: 'column', color: '#7B7B7B'}}>
                {/*Top nav bar */}
                <div style={{width: '100%', height: '70px', background: '#FFF96D', boxShadow: '2px 2px 10px #BCBCBC', display: 'flex', alignItems: 'center'}}>
                    <div onClick={() => this.setSelectedNav('shops')} style={{flexGrow: this.state.topNavSelected === 'shops' ? 1 : 0, height: '100%', transition: 'all 0.3s ease', minWidth: '20%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Shopping
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('accounts')} style={{flexGrow: this.state.topNavSelected === 'accounts' ? 1 : 0, minWidth: '20%', transition: 'all 0.3s ease', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Account
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('cart')} style={{flexGrow: this.state.topNavSelected === 'cart' ? 1 : 0, minWidth: '20%', height: '100%', transition: 'all 0.3s ease', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Cart
                    </div>
                    
                </div>

                <div style={{flexGrow: 1, width: '100%', display: 'flex', }}>
                    <div style={{height: '100%', width: '300px', maxWidth: '30%', background: 'white', }}>
                        {this.state.shoppingList.map((prop, key) =>
                            <div onClick={() => this.setOpenNavItem(key)} style={{cursor: 'pointer', maxHeight: prop.item ? this.state.openNavItem === key ? '90px' : '70px' : '70px', width: '100%', transition: 'all 0.2s ease', overflow: 'hidden'}}>
                                <div style={{width: '100%', height: '40px', display: 'flex'}}>
                                    <div style={{height: '100%', width: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: '30px'}}>{prop.item ? <MdRadioButtonChecked /> : <MdRadioButtonUnchecked />}</div>
                                    <input onClick={() => this.listItemClick(key)} onChange={(e) => this.setListItem(e, key)} value={prop.description} style={{border: 'none', borderBottom: '2px solid #7B7B7B', outline: 'none', background: 'transparent'}} />
                                </div>

                                {prop.item &&
                                    <div style={{height: '40px', width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'flex-start', }}>
                                        <div style={{flex: 1, marginLeft: '40px', flexShrink: 0}}>
                                            <div style={{minHeight: '30px', display: 'flex', flexShrink: 0, alignItems: 'center', fontSize: '12px'}}>
                                                {prop.item.brand +" " +prop.item.name}
                                            </div>
                                            <div style={{height: '20px', display: 'flex', alignItems: 'center', fontSize: '10px'}}>
                                                {prop.item.shopname}
                                            </div>
                                        </div>
                                        <div style={{height: '100%', marginRight: '10px', marginLeft: '10px'}}>{"R "+prop.item.amount}</div>
                                    </div>
                                }
                            </div>
                        )}

                        <div style={{marginTop: '10px', marginBottom: '10px', width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                <div onClick={this.createNewShoppingItem} style={{cursor: 'pointer', padding: '10px 20px', background: 'lightgrey', boxShadow: '0px 0px 2px #E2E2E2', fontWeight: 'bold'}}>add</div>
                        </div>

                    </div>

                    {
                        this.state.resultsIndex !== -1 ?
                            <div style={{height: '100%', width: 'calc(100% - 300px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                    <div style={{height: 'calc(100% - 40px)', width: 'calc(100% - 40px)', background: 'white', boxShadow: '2px 2px 10px #BCBCBC'}}>
                                        <div style={{width: '100%', height: '80px', display: 'flex', alignItems: 'center'}}>
                                            <div onClick={this.exitResults} style={{height: '100%', width: '80px', display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: '30px', color: "#7B7B7B", cursor: 'pointer'}}><MdKeyboardArrowLeft /></div>
                                            showing results for:
                                            <div style={{marginLeft: '10px', fontSize: '25px'}}>{this.state.shoppingList[this.state.resultsIndex].description}</div>
                                        </div>

                                        <div style={{width: '100%', display: 'flex', height: 'calc(100% - 80px)', overflowY: 'auto', overflowX: "hidden", flexWrap: 'wrap'}}>
                                            {
                                                this.state.results.map((prop, key) =>
                                                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '220px', width: '140px'}}>
                                                        <div onClick={() => this.setSelectedItemItem(prop)} style={{display: 'flex', border: this.state.shoppingList[this.state.resultsIndex].item && (prop.brand + " " + prop.name) === (this.state.shoppingList[this.state.resultsIndex].item.brand + " " + this.state.shoppingList[this.state.resultsIndex].item.name) ? '2px solid yellow' : '0', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center', textAlign: 'center'}}>
                                                            {prop.brand + " " + prop.name}
                                                        </div>
                                                    </div>
                                                    
                                                )
                                            }
                                        </div>
                                    </div>

                                    
                            </div>
                            :
                            this.state.shopPage !== "" ?
                                <div style={{height: '100%', width: 'calc(100% - 300px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                    <div style={{height: 'calc(100% - 40px)', width: 'calc(100% - 40px)', background: 'white', boxShadow: '2px 2px 10px #BCBCBC'}}>
                                        <div style={{width: '100%', height: '80px',  display: 'flex', alignItems: 'center', justifyContent: 'center', fontSize: '25px', fontWeight: 'bold'}}>
                                            <div onClick={this.closeStore} style={{height: '100%', width: '80px', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer'}}><MdKeyboardArrowLeft /></div>
                                            <div style={{flexGrow: 1, marginRight: '80px', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                                {this.state.shopPage}
                                            </div>
                                        </div>
                                        <div style={{width: '100%', display: 'flex', height: 'calc(100% - 80px)', overflowY: 'auto', overflowX: "hidden", flexWrap: 'wrap'}}>
                                            {
                                                this.state.items.map((prop, key) =>
                                                    prop.shopname === this.state.shopPage &&
                                                        <div style={{display: 'flex', margin: '10px', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center', textAlign: 'center'}}>
                                                            {prop.brand + " " + prop.name}
                                                        </div>
                                                    
                                                )
                                            }
                                        </div>
                                    </div>
                                </div>
                                :
                                <div style={{height: '100%', width: 'calc(100% - 300px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                    <div style={{height: 'calc(100% - 40px)', width: 'calc(100% - 40px)', background: 'white', boxShadow: '2px 2px 10px #BCBCBC'}}>
                                        <div style={{width: '100%', height: '80px',  display: 'flex', alignItems: 'center', justifyContent: 'center', fontSize: '25px', fontWeight: 'bold'}}>
                                            Shops around you
                                        </div>
                                        <div style={{width: '100%', display: 'flex', height: 'calc(100% - 80px)', overflowY: 'auto', overflowX: "hidden", flexWrap: 'wrap'}}>
                                            {
                                                this.state.shops.map((prop, key) =>
                                                    <div onClick={() => this.openStore(prop)} style={{cursor: 'point', display: 'flex', margin: '10px', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center', textAlign: 'center'}}>
                                                        {prop}
                                                    </div>
                                                )
                                            }
                                        </div>
                                    </div>
                                </div>
                            
                    }
                </div>
            </div>
        )
    }

    setSelectedItemItem = (item) => {
        var shoppingList = [].concat(this.state.shoppingList)
        shoppingList[this.state.resultsIndex].item = item
        this.setState({shoppingList})
    }

    createNewShoppingItem = () => {
        if(this.state.shoppingList.length && this.state.shoppingList[this.state.shoppingList.length - 1].description !== '') {
            var shoppingList = [].concat(this.state.shoppingList)
            var newShoppingItem  = {description: '', item: null}
            shoppingList.push(newShoppingItem)
            this.setState({shoppingList})
        }
        else if(!this.state.shoppingList.length) {
            var shoppingList = [].concat(this.state.shoppingList)
            var newShoppingItem  = {description: '', item: null}
            shoppingList.push(newShoppingItem)
            this.setState({shoppingList})
        }
    }

    closeStore = () => {
        this.setState({shopPage: ""})
    }

    openStore = (shopPage) => {
        this.setState({shopPage})
    }

    exitResults = () => {
        this.setState({resultsIndex: -1})
    }

    setListItem = (e, index) => {
        var shoppingList = this.state.shoppingList
        console.log(index)
        shoppingList[index].description = e.target.value
        this.setState({shoppingList, resultsIndex: index, results: []}, this.getResults )
    }

    listItemClick = (index) => {
        this.setState({resultsIndex: index, results: []}, this.getResults )
    }

    getResults = () => {
        var results = []

        for(var i = 0; i < this.state.items.length; i++) {
            if(this.state.items[i].name.toUpperCase().includes(this.state.shoppingList[this.state.resultsIndex].description.toUpperCase()))
                results.push(this.state.items[i])
        }

        this.setState({results})
    }

    setOpenNavItem = (openNavItem) => {
        if(openNavItem === this.state.openNavItem)
            this.setState({openNavItem: -1})
        else 
            this.setState({openNavItem})

    }

    setSelectedNav = (topNavSelected) => {
        this.setState({topNavSelected})
    }

    test = async () => {
        const response = await fetch(this.props.url+'/api/items');
        console.log(response)
        const data = await response.json();
        console.log(data)
        var shops = []
        
        for(var i = 0; i < data.length; i++) {
            if(shops.indexOf(data[i].shopname) === -1)
                shops.push(data[i].shopname)
        }

        console.log(shops)

        this.setState({items: data, shops: shops})
    }
}

